                this.vip[i].vip = ''
              }
              if(this.vip[i].verifyUser == true){
                this.vip[i].verifyUser = ''
              }else{
                this.vip[i].verifyUser = ''
              }
            }
          },
        ).catch((e) => {
        if (e && e.response) {
          switch (e.response.status) {
            case 504:
              this.$message({
                showClose: true,
                message: '',
                type: 'warning'
              });
              this.loading = false;//
              break
            case 500:
              this.$message({
                showClose: true,
                message: '',
                type: 'warning'
              });
              this.loading = false;//
              break
            case 405:
              this.$message({
                showClose: true,
                message: '',
                type: 'warning'
              });
              break
          }
        }
      });
    }
  }
</script>

<style>
  .demo-table-expand label {
    font-weight: bold;
  }
  .toolbar {
    margin-top: 30px;
  }
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 100%;
  }
  .el-dialog__body{
   padding-bottom:60px;
  }
  .el-dialog__wrapper:nth-child(7){
    left:10%;
  }
  .el-dialog__wrapper:nth-child(7) .el-dialog{
    width:90%;
  }
</style>




